.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  color: #333;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-section {
  margin: 20px;
  padding: 50px 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  text-align: center;
}

.App-footer {
  background-color: #282c34;
  color: white;
  padding: 20px;
  text-align: center;
}

.App-hero {
  background: linear-gradient(135deg, #282c34, #4a90e2);
  color: white;
  text-align: center;
  padding: 100px 20px;
}

.App-hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.App-title {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.App-subtitle {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.App-cta {
  display: inline-block;
  padding: 10px 20px;
  background-color: #61dafb;
  color: #282c34;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
}

.App-cta:hover {
  background-color: #21a1f1;
}

.App-skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.App-skills span {
  background-color: #f1f1f1;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 0.9rem;
}

.App-projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.App-project-card {
  background-color: #f9f9f9;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 300px;
  text-align: left;
}
